.gameInstructions {
    background-color: green; 

    padding: 20px 20px;
    border-radius: 15px;
    margin-top: 150px;
    width: 100%;
    text-align: center;
    font-size: 2rem;
    text-decoration: underline;
    color: white;
}

.gameInstructions:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
}


